body{-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none
}
.project__title{color:white;font-family:"butler_regular",serif;text-transform:uppercase;font-size:40px;line-height:64px;letter-spacing:.2em
}
@media only screen and (min-width: 1024px){
