}
}
.project__title .blur-letter{color:white;display:inline-block;opacity:0;filter:blur(20px)
}
.project__title>.blur-letter{display:none
}
@media only screen and (min-width: 1024px){
.project__title>.blur-letter{display:inline-block
}
}
.project__title .title__mobile{font-size:34px
}
