
@font-face{font-family:'butler_stencilbold';src:url("/static/fonts/butler_bold_stencil-webfont.woff2") format("woff2"),url("/static/fonts/butler_bold_stencil-webfont.woff") format("woff");font-weight:bold;font-style:normal
}
@font-face{font-family:'butler_regular';src:url("/static/fonts/butler_regular_stencil-webfont.woff2") format("woff2"),url("/static/fonts/butler_regular_stencil-webfont.woff") format("woff");font-weight:normal;font-style:normal
}
@font-face{font-family:'gt_eesti_pro_displaylight';src:url("/static/fonts/gteestiprodisplay-light-webfont.woff2") format("woff2"),url("/static/fonts/gteestiprodisplay-light-webfont.woff") format("woff");font-weight:normal;font-style:normal
}
@font-face{font-family:'gt_eesti_pro_displayultraLt';src:url("/static/fonts/gteestiprodisplay-ultralight-webfont.woff2") format("woff2"),url("/static/fonts/gteestiprodisplay-ultralight-webfont.woff") format("woff");font-weight:normal;font-style:normal
}
body{-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none
}
body{background:#000
}
.page--animated{opacity:0;filter:blur(100px);transition:filter ease-out 3s, opacity ease-out 1.5s
}
.loader{display:-ms-flexbox;display:flex;position:fixed;top:0;left:0;width:100vw;height:100vh;background:#000;transition:all ease .2s;-ms-flex-pack:center;justify-content:center;pointer-events:none;opacity:0
}
.loader__text{color:#fff;-ms-flex-item-align:center;-ms-grid-row-align:center;align-self:center;font-size:14px;letter-spacing:9px;font-family:"gt_eesti_pro_displayultraLt",sans-serif
}
.loader__text span{filter:blur(10px);opacity:0.5
}
.loader--visible{opacity:1;pointer-events:all
}



