body{-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none
}
.page--about{position:relative;overflow:hidden
}
.page--about .about__ctn{display:-ms-flexbox;display:flex;min-height:calc(100vh - 75px);padding:30px;margin:0 auto;color:#fff
}
@media only screen and (min-width: 1024px){
.page--about .about__ctn{max-width:1024px
}
}
.page--about .about__ctn .about__content{display:-ms-flexbox;display:flex;-ms-flex-direction:column;flex-direction:column;-ms-flex-item-align:center;align-self:center;width:100%
}
@media only screen and (min-width: 1024px){
.page--about .about__ctn .about__content{position:relative
}
}
.page--about .about__ctn .about__content .animated-rectangle{position:absolute;left:-30px;top:4px
}
.page--about .about__ctn .about__picture--desktop{display:none;position:absolute;margin-top:20px;top:50%;right:4vw;width:530px;height:594px;transform:translate3d(0, -50%, 0);opacity:0;animation:pictureEnter 1.2s forwards;animation-delay:.8s;background-color:black;background-image:url("/static/images/about/picture.png");background-blend-mode:hard-light;z-index:-1
}
@media only screen and (min-width: 1024px){
.page--about .about__ctn .about__picture--desktop{display:block
}
}
.page--about .about__ctn .about__picture--mobile{opacity:0.2;mix-blend-mode:color-dodge;position:absolute;top:20px;left:50%;width:70vw;transform:translate3d(-50%, 0, 0)
}
.page--about .about__ctn .about__picture--mobile img{max-width:100%
}
@media only screen and (min-width: 1024px){
.page--about .about__ctn .about__picture--mobile{display:none
}
}
.page--about .project__next{position:fixed;bottom:0;left:50%;max-width:1024px;transform:translate3d(-50%, 0, 0)
}
.page--about:after{content:'';background:url("/static/images/noise-background.png");animation:noiseEnter 1s forwards, noiseMove .3s infinite;position:fixed;pointer-events:none;z-index:20;top:0;left:0;width:100vw;height:100vh;opacity:0
}
@keyframes noiseMove{
0%,100%{background-position:center center
}
50%{background-position:200px 200px
}
}
@keyframes noiseEnter{
0%{opacity:0
}
100%{opacity:.8
}
}
@keyframes pictureEnter{
0%{opacity:0
}
100%{opacity:.4
}
}



